import './Message.css';
import SmartToyTwoToneIcon from '@mui/icons-material/SmartToyTwoTone';
import PersonOutlineTwoToneIcon from '@mui/icons-material/PersonOutlineTwoTone';
import SentimentVerySatisfiedTwoToneIcon from '@mui/icons-material/SentimentVerySatisfiedTwoTone';
import Avatar from '@mui/material/Avatar';
import SoupKitchenTwoToneIcon from '@mui/icons-material/SoupKitchenTwoTone';
import SelfImprovementTwoToneIcon from '@mui/icons-material/SelfImprovementTwoTone';


interface IMessage {
    request: {
        key: string,
        text: string
    },
    response: {
        key: string,
        text: string
    }
}

export default function Message({request, response}: {request: any, response: any}) {
    return (
        <>
            <div className="MessageRequest" key={request.key} id={request.key}>
                <Avatar className="Avatar" key={"avatar" + request.key} variant="square" sx={{ backgroundColor: "#005757", maxHeight: 25, maxWidth: 25, minHeight: 25, minWidth: 25, borderRadius: 1 }}>
                    <SelfImprovementTwoToneIcon key={"icon" + request.key} sx={{ fontSize: 22, color: '#60FF6A' }} />
                </Avatar>
                <p className="MessageParagraph" id={"p" + request.key}>{request.text}</p>
            </div>
            <div className="MessageResponse" key={response.key} id={response.key}>
                <Avatar className="Avatar" key={"avatar" + response.key} variant="square" sx={{ backgroundColor: "#830000", maxHeight: 25, maxWidth: 25, minHeight: 25, minWidth: 25, borderRadius: 1 }}>
                    <SoupKitchenTwoToneIcon  key={"icon" + response.key} sx={{ fontSize: 22, color: '#FF4040' }} />
                </Avatar>
                <p className="MessageParagraph" id={"p" + response.key}>{response.text}</p>
            </div>
        </>
    )   
}