import './App.css';
import Chat from './Chat';
import Sidebar from './Sidebar';
import Fab from '@mui/material/Fab';
import MenuBookIcon from '@mui/icons-material/MenuBook';
import { useState } from 'react';
import { Collapse } from '@mui/material';

interface IMessage {
  request: {
      key: string,
      text: string
  },
  response: {
      key: string,
      text: string
  },
  key: string
}

function App() {
  const [menu, setMenu] = useState(false)
  const [messages, setMessages] = useState<IMessage[]>([]);

  function toggleMenu() {
    let appHeader = document.getElementById("app-header")
    if (menu === true) {
      setMenu(false)
      if (appHeader) {
        appHeader.style.width = '100%'
      }
    }
    else {
      setMenu(true)
      if (appHeader) {
        appHeader.style.width = 'calc(100% - 260px)'
      }
    }
  }
  return (
    <div className="App">
      <Fab className="fab" onClick={toggleMenu} disableRipple size="small" sx={{position: 'absolute', color: 'white', top: 10, left: 10, borderRadius: '5px', backgroundColor: "#1B1F23", borderStyle: 'solid', borderWidth: '1px', borderColor: 'aqua', "&:hover": {backgroundColor: "#2d3238"}}}>
          <MenuBookIcon />
      </Fab>
      <Collapse in={menu}>
        <Sidebar messages={messages}></Sidebar>
      </Collapse>
      <header id="app-header" className="App-header">
        <Chat messages={messages} setMessages={setMessages}></Chat>
      </header>
    </div>
  );
}

export default App;
