import React, { useState } from 'react';
import './Chat.css'
import UserEntry from './UserEntry';
import Message from './Message'
import WelcomeMessage from './WelcomeMessage';

interface IMessage {
    request: {
        key: string,
        text: string
    },
    response: {
        key: string,
        text: string
    },
    key: string
}

export default function Chat({messages, setMessages}: {messages: Array<IMessage>, setMessages: React.Dispatch<React.SetStateAction<Array<IMessage>>>}) {
    const [inputValue, setInputValue] = useState("");
    // const [messages, setMessages] = useState<IMessage[]>([]);
    // const [pastRecipes, setPastRecipes] = useState<String[]>([]);


    function Messages() {
        let Messages = messages.map((message: { request: { key: string, text: string }, response: { key: string, text: string }, key: string}) => { 
            return <Message request={{key: '123', text: 'https://www.youtube.com/watch?v=fbNd3e8c8Ns'}} response={{key: '234', text: 'Birria Tacos Recipe:\n' +
            '\n' +
            'Ingredients:\n' +
            '- Ancho chili\n' +
            '- Arbol chili\n' +
            '- Guajillo chili\n' +
            '- Avocado oil (spray)\n' +
            '- Onion\n' +
            '- Tomato paste\n' +
            '- Bay leaf\n' +
            '- Cinnamon stick\n' +
            '- Cloves\n' +
            '- Tomato (chopped)\n' +
            '- Water or stock\n' +
            '- Chicken breast (boneless, skinless)\n' +
            '- Thyme\n' +
            '- Black pepper\n' +
            '- Oregano (or use fresh marjoram)\n' +
            '- Cumin\n' +
            '- Coriander\n' +
            '- Salt\n' +
            '- White onion (diced)\n' +
            '- Fresh cilantro\n' +
            '- Oaxaca cheese (or low-fat mozzarella)\n' +
            '- Corn tortillas (or preferred tortilla option)\n' +
            '- Consume (to keep meat hydrated)\n' +
            '- Optional: flour tortillas (for extra options)\n' +
            '\n' +
            'Instructions:\n' +
            '1. Deseed the ancho, airborne, and guajillo chilies, then rough chop them.\n' +
            '2. Heat a pan and add a tiny spray of avocado oil. Add onions and cook until softened.\n' +
            '3. Add the chopped chilies to the pan and cook for a few minutes.\n' +
            '4. Stir in tomato paste, bay leaf, cinnamon stick, cloves, and chopped tomato. Cover with water or stock.\n' +
            '5. Simmer the mixture until it punches into the nose.\n' +
            '6. Transfer the slightly cooled mixture to a blender. Add thyme, black pepper, oregano, cumin, coriander, and salt. Blend until smooth.\n' +
            '7. Return the blended mixture to the pan and bring it to a light bubble.\n' +
            '8. In a separate bowl, shred boneless, skinless chicken breasts using two forks.\n' +
            '9. Add the shredded chicken to the pan and simmer for about an hour or until fork-tender.\n' +
            '10. Dice white onions and chop fresh cilantro for toppings.\n' +
            "11. Shred the Oaxaca cheese (or preferred cheese) while it's firm.\n" +
            '12. Toast corn tortillas (or preferred tortilla option) in a separate pan.\n' +
            '13. Assemble the tacos with shredded chicken, cheese, onions, and cilantro.\n' +
            '14. Optional: Use flour tortillas instead of corn tortillas.\n' +
            '15. Serve the tacos crispy and enjoy!\n' +
            '\n' +
            'Additional Tips:\n' +
            '- This recipe is meal preppable, and the chicken can be reheated for a couple of days.\n' +
            '- Use low-fat mozzarella if you prefer lower-calorie cheese.\n' +
            '- For a gluten-free option, use gluten-free tortillas.\n' +
            '- Control the amount of tortilla and cheese for a lower-calorie taco.\n' +
            '- Consider adding consume to keep the meat hydrated.\n' +
            "- Shred the cheese while it's firm for easy handling.\n" +
            '- Use two forks to shred the chicken to avoid burning your hands.\n' +
            '- Experiment with different toppings and tortilla varieties for unique flavors.\n' +
            '- Enjoy the tacos and save the recipe for future use!\n'}} key='456'></Message>})
        if (Messages.length == 0) {
            return <WelcomeMessage></WelcomeMessage>
        } 
        // else {
        //     console.log(messages.slice(-1)[0].response.text)
        //     console.log(pastRecipes)
        // }
        
        return Messages;
    }


    return (
        <>
            <div className='Chat' id="Chat">
                {Messages()}
            </div>
            <div className='Fade'></div>
            <UserEntry inputValue={inputValue} setInputValue={setInputValue} chatMessages={messages} setChatMessages={setMessages}></UserEntry>
        </>
    )
}